	//下拉刷新
	onPullDownRefresh() {
		console.log('下拉');
		setTimeout(function() {
			uni.stopPullDownRefresh(); //结束下拉动画
		}, 1000);
	},
	//上拉加载
	onReachBottom() {
		let _self = this;
		_self.status = 'loading'; //赋值加载中
		this.load();
	},
	methods: {
		load: function() {
			let index = this.recomItem.length;
			let id = this.recomItem[index - 1].id;
			id++;
			if (id < 5) {
				let loadData = {
					name: '测试' + id,
					id: id
				};
				this.recomItem.push(loadData);
				this.status = 'more'; //赋值查看更多
			} else {
				this.status = 'noMore'; //赋值查看更多
			}
		},
